import React, { useEffect, useState } from 'react';
import { sha256 } from "js-sha256"
import { Link, useNavigate } from "react-router-dom";

const Login = () => {
    let navigate = useNavigate()
    const [wrongPassword, setWrongPassword] = useState(false)
    const loginHandler = (e) => {
        const email = document.getElementById("email").value
        const password = sha256(document.getElementById("password").value)
        const data_user = {
            email,
            password
        }
        try {
            var myHeaders = new Headers();
            myHeaders.append("Content-Type", "application/json");

            var requestOptions = {
                method: 'POST',
                headers: myHeaders,
                body: JSON.stringify(data_user)
            };
            fetch("https://pandorademo-be.onrender.com/usernonauth/login", requestOptions)
            .then(response => response.text())
            .then(result => {
                if(result=="wrong password"){
                    setWrongPassword(true)
                }else{
                    const resp = JSON.parse(result)
                    localStorage.setItem("token",resp["token"])
                    navigate("/")
                }
            })
		} catch (error) {
			console.log(error)
		}
    }
	return (
    <section class="vh-100" style={{backgroundColor: "#02b0a7"}}>
        <div class="container py-5 h-100">
            <div class="row d-flex justify-content-center align-items-center h-100">
            <div class="col-12 col-md-8 col-lg-6 col-xl-5">
                <div class="card shadow-2-strong" style={{borderRadius: "1rem"}}>
                <div class="card-body p-5 text-center">

                    <h3 class="mb-5">Sign in</h3>

                    <div class="form-outline mb-4">
                    <input type="email" id="email" class="form-control form-control-lg" />
                    <label class="form-label" for="typeEmailX-2">Email</label>
                    </div>

                    <div class="form-outline mb-4">
                    <input type="password" id="password" class="form-control form-control-lg" />
                    <label class="form-label" for="typePasswordX-2">Password</label>
                    </div>
                    {wrongPassword && <p className='text-danger'>Wrong Password</p>}
                    <button class="btn btn-primary btn-lg btn-block" type="submit" onClick={loginHandler}>Login</button>
                    <p>Don’t have an account? <a href="/signup">Sign up</a></p>

                    <hr class="my-4"/>

                </div>
                </div>
            </div>
            </div>
        </div>
    </section>
	)
}

export default Login