import React, { useEffect, useState } from 'react';
import { sha256 } from "js-sha256"
import { Link, useNavigate } from "react-router-dom";

const Signup = () => {
	//preparing library needed
    let navigate = useNavigate();
    const [userChoose, setUserChoose] = useState()
    const [emaiTaken, setEmailTaken] = useState(false)
    const signupHandler = (e) => {
        const email = document.getElementById("email").value
        const name = document.getElementById("name").value
        const password = sha256(document.getElementById("password").value)
        let country = ""
        if(userChoose=="indonesia"){
            country = "IDN"
        }else{
            country = "KRW"
        }
        const bank_account = ""
        const data_user = {
            name,
            email,
            password,
            country,
            bank_account
        }
		try {
            var myHeaders = new Headers();
            myHeaders.append("Content-Type", "application/json");

            var requestOptions = {
                method: 'POST',
                headers: myHeaders,
                body: JSON.stringify(data_user)
            };
            fetch("https://pandorademo-be.onrender.com/usernonauth/signup", requestOptions)
            .then(response => response.text())
            .then(result => {
                if(result=="success"){
                    navigate("/login")
                }else{
                    setEmailTaken(true)
                }
            })
		} catch (error) {
			console.log(error)
		}
    }

	return (
    <section class="vh-100" style={{backgroundColor: "#02b0a7"}}>
        <div class="container py-5 h-100">
            <div class="row d-flex justify-content-center align-items-center h-100">
            <div class="col-12 col-md-8 col-lg-6 col-xl-5">
                <div class="card shadow-2-strong" style={{borderRadius: "1rem"}}>
                <div class="card-body p-5 text-center">

                    <h3 class="mb-5">Sign Up</h3>

                    <div class="form-outline mb-4">
                    <input type="email" id="email" class="form-control form-control-lg" />
                    <label class="form-label" for="typeEmailX-2">Email</label>
                    </div>

                    <div class="form-outline mb-4">
                    <input type="text" id="name" class="form-control form-control-lg" />
                    <label class="form-label" for="typePasswordX-2">Name</label>
                    </div>

                    <div class="form-outline mb-4">
                    <input type="password" id="password" class="form-control form-control-lg" />
                    <label class="form-label" for="typePasswordX-2">Password</label>
                    </div>

                    <div class="form-outline mb-4">
                    <div className="d-flex h-50 mb-3 justify-content-between w-100">
                        <button className={userChoose=="indonesia"?"w-50 bg-success text-white border-success h-100 rounded-2":"w-50 border-white h-100 rounded-2"} onClick={(e)=>{e.preventDefault();setUserChoose("indonesia")}}>Indonesia</button>
                        <button className={userChoose=="southkorea"?"w-50 bg-success text-white border-success h-100 rounded-2":"w-50 border-white h-100 rounded-2"} onClick={(e)=>{e.preventDefault();setUserChoose("southkorea")}}>South Korea</button>
                    </div>
                    <label class="form-label" for="typePasswordX-2">Country</label>
                    </div>
                    {emaiTaken && <p className='text-danger'>Email exist</p>}
                    <button class="btn btn-primary btn-lg btn-block" type="submit" onClick={signupHandler}>Submit</button>
                    <p>have an account? <a href="/login">Sign in</a></p>

                    <hr class="my-4"/>

                </div>
                </div>
            </div>
            </div>
        </div>
    </section>
	)
}

export default Signup