import React, { useEffect, useState } from 'react';

const Receive = () => {
	//preparing library needed
    const token = localStorage.getItem("token")
	const [isLogin, setIsLogin] = useState(false)
	const [dataUser, setDataUser] = useState({name:"",amount:""})
    const [dataInvoice, setDataInvoice] = useState({qr_code:"",id_invoice:""})
    const [isInvoice, setIsInvoice] = useState(false)
	useEffect(()=>{
		const token = localStorage.getItem("token")
		if(token != undefined){
			setIsLogin(true)
			try {
				var myHeaders = new Headers();
				myHeaders.append("Content-Type", "application/json");
				myHeaders.append("Authorization", "Bearer "+token);
	
				var requestOptions = {
					method: 'GET',
					headers: myHeaders
				};
				fetch("https://pandorademo-be.onrender.com/user/getdata", requestOptions)
				.then(response => response.text())
				.then(result => {
					const res = JSON.parse(result)
					setDataUser({name:res["name"],
                    amount:new Intl.NumberFormat().format(res["amount"]),
                    country:res["country"]
                })
				})
			} catch (error) {
				console.log(error)
			}
		}
	},[])

    const createInvoice = (e) => {
        e.preventDefault()
        // setIsInvoice(true)
        const data_send = {
            "name":dataUser["name"],
            amount:document.getElementById("amount").value,
            "country":dataUser["country"]
        }
        try {
            var myHeaders = new Headers();
            myHeaders.append("Content-Type", "application/json");
            myHeaders.append("Authorization", "Bearer "+token);

            var requestOptions = {
                method: 'POST',
                headers: myHeaders,
                body:JSON.stringify(data_send)
            };
            fetch("https://pandorademo-be.onrender.com/receiver/createinvoicecrossborder", requestOptions)
            .then(response => response.text())
            .then(result => {
                const res = JSON.parse(result)
                setIsInvoice(true)
                setDataInvoice(res)
            })
        } catch (error) {
            console.log(error)
        }
    }
	return (
        <section class="vh-100" style={{backgroundColor: "#02b0a7"}}>
        <div class="container py-5 h-100">
            <div class="row d-flex justify-content-center align-items-center h-100">
				<div class="col-12 col-md-8 col-lg-6 col-xl-5">
					<div class="card shadow-2-strong" style={{borderRadius: "1rem"}}>
						<nav className="navbar navbar-expand-lg navbar-light bg-light m-4 rounded shadow">
								<div className="container-fluid">
									<a className="navbar-brand" href="/">Pandora</a>
									<button className="navbar-toggler" type="button"
										data-bs-toggle="collapse" data-bs-target="#navbarNav"
										aria-controls="navbarNav" aria-expanded="false"
										aria-label="Toggle navigation">
										<span className="navbar-toggler-icon"></span>
									</button>
									<div className="collapse navbar-collapse" id="navbarNav">
										<ul className="navbar-nav ms-auto">
											<li className="nav-item">
												<a className="nav-link active" aria-current="page" href="/">Home</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/profile">Profile</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/deposit">Deposit</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/withdraw">Withdraw</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/" onClick={()=>{localStorage.removeItem("token")}}>Sign out</a>
											</li>
										</ul>
									</div>
								</div>
							</nav>
						{!isInvoice && isLogin && <div class="card-body p-5 text-center">
                            <h3 class="mb-5">Create Invoice</h3>

                            <div class="form-outline mb-4">
                            <input type="number" id="amount" class="form-control form-control-lg text-center" />
                            <label class="form-label" for="typeEmailX-2">amount</label>
                            </div>

                            <button class="btn btn-primary btn-lg btn-block" type="submit" onClick={createInvoice}>Create Payment</button>

							<hr class="my-4"/>
						</div>}
                        {isInvoice && isLogin && <div class="card-body p-5 text-center">
                            <h3 class="mb-5">Invoice Information</h3>
                            <img src={dataInvoice["qr_code"]}/>
                            <p>Invoice Id</p>
                            <p>{dataInvoice["id_invoice"]}</p>
							<hr class="my-4"/>
						</div>}
                        {!isLogin && 
						<div class="card-body p-5 text-center">
							<p>You have to login first</p>
							<button class="btn btn-primary btn-lg btn-block m-1" type="submit" onClick={()=>{window.location.href="/login"}}>Login</button>
						</div>
						}
					</div>
				</div>
            </div>
        </div>
    </section>
	)
}

export default Receive