import React, { useEffect, useState } from 'react';
import { Link, useNavigate } from "react-router-dom";

const Send = () => {
	let navigate = useNavigate();
    const token = localStorage.getItem("token")
	const [isLogin, setIsLogin] = useState(false)
	const [dataUser, setDataUser] = useState({name:"",amount:""})
	const [dataInvoice, setDataInvoice] = useState({qr_code:"",id_invoice:""})
    const [isInvoice, setIsInvoice] = useState(false)
	const [idInvoice, setIdInvoice] = useState("")
	useEffect(()=>{
		const token = localStorage.getItem("token")
		if(token != undefined){
			setIsLogin(true)
			try {
				var myHeaders = new Headers();
				myHeaders.append("Content-Type", "application/json");
				myHeaders.append("Authorization", "Bearer "+token);
	
				var requestOptions = {
					method: 'GET',
					headers: myHeaders
				};
				fetch("https://pandorademo-be.onrender.com/user/getdata", requestOptions)
				.then(response => response.text())
				.then(result => {
					const res = JSON.parse(result)
					setDataUser({name:res["name"],
                    amount:new Intl.NumberFormat().format(res["amount"]),
                    country:res["country"]
                })
				})
			} catch (error) {
				console.log(error)
			}
		}
	},[])
    
	const checkInvoice = (e) => {
		e.preventDefault()
		const data_send = {
            id_invoice:document.getElementById("id_invoice").value
        }
		setIdInvoice(data_send["id_invoice"])
        try {
            var myHeaders = new Headers();
            myHeaders.append("Content-Type", "application/json");
            myHeaders.append("Authorization", "Bearer "+token);

            var requestOptions = {
                method: 'POST',
                headers: myHeaders,
                body:JSON.stringify(data_send)
            };
            fetch("https://pandorademo-be.onrender.com/sender/checkinvoice", requestOptions)
            .then(response => response.text())
            .then(result => {
                const res = JSON.parse(result)
                setIsInvoice(true)
                setDataInvoice(res)
            })
        } catch (error) {
            console.log(error)
        }
	}

	const payInvoice = (e) => {
		e.preventDefault()
		const data_send = {
            id_invoice:idInvoice
        }
        try {
            var myHeaders = new Headers();
            myHeaders.append("Content-Type", "application/json");
            myHeaders.append("Authorization", "Bearer "+token);

            var requestOptions = {
                method: 'POST',
                headers: myHeaders,
                body:JSON.stringify(data_send)
            };
            fetch("https://pandorademo-be.onrender.com/sender/payinvoice", requestOptions)
            .then(response => response.text())
            .then(result => {
				if(result=="payment success"){
					navigate("/")
				}
            })
        } catch (error) {
            console.log(error)
        }
	}
	return (
        <section class="vh-100" style={{backgroundColor: "#02b0a7"}}>
        <div class="container py-5 h-100">
            <div class="row d-flex justify-content-center align-items-center h-100">
				<div class="col-12 col-md-8 col-lg-6 col-xl-5">
					<div class="card shadow-2-strong" style={{borderRadius: "1rem"}}>
						<nav className="navbar navbar-expand-lg navbar-light bg-light m-4 rounded shadow">
								<div className="container-fluid">
									<a className="navbar-brand" href="/">Pandora</a>
									<button className="navbar-toggler" type="button"
										data-bs-toggle="collapse" data-bs-target="#navbarNav"
										aria-controls="navbarNav" aria-expanded="false"
										aria-label="Toggle navigation">
										<span className="navbar-toggler-icon"></span>
									</button>
									<div className="collapse navbar-collapse" id="navbarNav">
										<ul className="navbar-nav ms-auto">
											<li className="nav-item">
												<a className="nav-link active" aria-current="page" href="/">Home</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/profile">Profile</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/deposit">Deposit</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/withdraw">Withdraw</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/" onClick={()=>{localStorage.removeItem("token")}}>Sign out</a>
											</li>
										</ul>
									</div>
								</div>
							</nav>
						{isLogin && !isInvoice && <div class="card-body p-5 text-center">
                            <h3 class="mb-5">Pay Invoice</h3>

                            <div class="form-outline mb-4">
                            <input type="text" id="id_invoice" class="form-control form-control-lg text-center" />
                            <label class="form-label" for="typeEmailX-2">id invoice</label>
                            </div>

                            <button class="btn btn-primary btn-lg btn-block" type="submit" onClick={checkInvoice}>Check Invoice</button>

							<hr class="my-4"/>
						</div>}
						{isLogin && isInvoice && <div class="card-body p-5 text-center">
                            <h3 class="mb-5">Pay Invoice</h3>

                            <div class="form-outline mb-4">
								<h4>Send to: {dataInvoice.name_receiver}</h4>
								<h5>Amount: {Intl.NumberFormat().format(dataInvoice.amount_price)}</h5>
                            </div>

                            <button class="btn btn-primary btn-lg btn-block" type="submit" onClick={payInvoice}>Pay</button>

							<hr class="my-4"/>
						</div>}
					</div>
				</div>
            </div>
        </div>
    </section>
	)
}

export default Send