import React, { useEffect, useState } from 'react';
import collapse from 'bootstrap'

const Home = () => {
	//preparing library needed
	const [isLogin, setIsLogin] = useState(false)
	const [dataUser, setDataUser] = useState({name:"",amount:"", country:""})
	useEffect(()=>{
		const token = localStorage.getItem("token")
		if(token != undefined){
			setIsLogin(true)
			try {
				var myHeaders = new Headers();
				myHeaders.append("Content-Type", "application/json");
				myHeaders.append("Authorization", "Bearer "+token);
	
				var requestOptions = {
					method: 'GET',
					headers: myHeaders
				};
				fetch("https://pandorademo-be.onrender.com/user/getdata", requestOptions)
				.then(response => response.text())
				.then(result => {
					const res = JSON.parse(result)
					console.log(res)
					if(res["country"]=="KRW"){
						setDataUser({name:res["name"],amount:new Intl.NumberFormat().format(res["amount"]),country:"KRW"})
					}else{
						setDataUser({name:res["name"],amount:new Intl.NumberFormat().format(res["amount"]),country:"IDR"})
					}
				})
			} catch (error) {
				console.log(error)
			}
		}
	},[])

	return (
	<section class="vh-100" style={{backgroundColor: "#02b0a7"}}>
        <div class="container py-5 h-100">
            <div class="row d-flex justify-content-center align-items-center h-100">
				<div class="col-12 col-md-8 col-lg-6 col-xl-5">
					<div class="card shadow-2-strong" style={{borderRadius: "1rem"}}>
						<nav className="navbar navbar-expand-lg navbar-light bg-light m-4 rounded shadow">
								<div className="container-fluid">
									<a className="navbar-brand" href="/">Pandora</a>
									<button className="navbar-toggler" type="button"
										data-bs-toggle="collapse" data-bs-target="#navbarNav"
										aria-controls="navbarNav" aria-expanded="false"
										aria-label="Toggle navigation">
										<span className="navbar-toggler-icon"></span>
									</button>
									<div className="collapse navbar-collapse" id="navbarNav">
										<ul className="navbar-nav ms-auto">
											<li className="nav-item">
												<a className="nav-link active" aria-current="page" href="/">Home</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/profile">Profile</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/deposit">Deposit</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/withdraw">Withdraw</a>
											</li>
											<li className="nav-item">
												<a className="nav-link" href="/" onClick={()=>{localStorage.removeItem("token")}}>Sign out</a>
											</li>
										</ul>
									</div>
								</div>
							</nav>
						{isLogin && <div class="card-body p-5 text-center">
							<div class="mb-5">
								<h1>Welcome {dataUser.name}</h1>
								<h3>{dataUser.country}{dataUser.amount}</h3>
							</div>
							<button class="btn btn-primary btn-lg btn-block m-1" type="submit" onClick={()=>{window.location.href="/send"}}>Send</button>
							<button class="btn btn-primary btn-lg btn-block m-1" type="submit" onClick={()=>{window.location.href="/receive"}}>Receive</button>

							<hr class="my-4"/>
						</div>}
						{!isLogin && 
						<div class="card-body p-5 text-center">
							<p>You have to login first</p>
							<button class="btn btn-primary btn-lg btn-block m-1" type="submit" onClick={()=>{window.location.href="/login"}}>Login</button>
						</div>
						}
					</div>
				</div>
            </div>
        </div>
    </section>
	)
}

export default Home